.navigation {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: $light_text_color;

  .mobile-menu-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 10vh;
    font-size: 30px;


    p {
      display: none;
    }

    .mobile-menu-box:active {
      transform: translateY(3px) scale(1.1);
    }
  }

  a {
    text-decoration: none;
    color: $dark_accent_color;
  }

  a:hover {
    transform: translateY(3px) scale(1.1);
  }

  a:focus {
    transform: translateY(3px) scale(1.1);
  }

}


.hidden {
  display: none;
}


.pause-button {
  padding-top: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 30px;
  font-size: 45px;
}